.meeting .v-wrapper .loading-wrapper {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.meeting .v-wrapper .loading-wrapper .loading {
  width: 40px;
  height: 40px;
  position: relative;
}
.meeting .v-wrapper .loading-wrapper .loading .circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.meeting .v-wrapper .loading-wrapper .loading .circle span {
  width: 12px;
  height: 12px;
  display: inline-block;
  background: #fff;
  border-radius: 100%;
  position: absolute;
  -webkit-animation: mycircle 1.2s infinite ease-in-out;
  animation: mycircle 1.2s infinite ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.meeting .v-wrapper .loading-wrapper .loading .circle2 {
  transform: rotateZ(45deg);
}
.meeting .v-wrapper .loading-wrapper .loading .circle3 {
  transform: rotateZ(90deg);
}
.meeting .v-wrapper .loading-wrapper .loading .circle > span:nth-child(1) {
  top: 0;
  left: 0;
}
.meeting .v-wrapper .loading-wrapper .loading .circle > span:nth-child(2) {
  top: 0;
  right: 0;
}
.meeting .v-wrapper .loading-wrapper .loading .circle > span:nth-child(3) {
  right: 0;
  bottom: 0;
}
.meeting .v-wrapper .loading-wrapper .loading .circle > span:nth-child(4) {
  left: 0;
  bottom: 0;
}
.meeting .v-wrapper .loading-wrapper .loading .circle2 > span:nth-child(1) {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle3 > span:nth-child(1) {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle1 > span:nth-child(2) {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle2 > span:nth-child(2) {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle3 > span:nth-child(2) {
  -webkit-animation-delay: -0.7s;
  animation-delay: -0.7s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle1 > span:nth-child(3) {
  -webkit-animation-delay: -0.6s;
  animation-delay: -0.6s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle2 > span:nth-child(3) {
  -webkit-animation-delay: -0.7s;
  animation-delay: -0.7s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle3 > span:nth-child(3) {
  -webkit-animation-delay: -0.4s;
  animation-delay: -0.4s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle1 > span:nth-child(4) {
  -webkit-animation-delay: -0.3s;
  animation-delay: -0.3s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle2 > span:nth-child(4) {
  -webkit-animation-delay: -0.2s;
  animation-delay: -0.2s;
}
.meeting .v-wrapper .loading-wrapper .loading .circle3 > span:nth-child(4) {
  -webkit-animation-delay: -0.1s;
  animation-delay: -0.1s;
}
@-webkit-keyframes mycircle {
  0% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
  80% {
    transform: scale(0);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes mycircle {
  0% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
  80% {
    transform: scale(0);
  }
  100% {
    transform: scale(0);
  }
}
.meeting .v-wrapper.loading:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 50%;
  border: 2px #f0dcd0 solid;
  clip: rect(0, 40px, 160px, 0);
  z-index: 3;
  -webkit-animation: loading 1.1s infinite linear;
          animation: loading 1.1s infinite linear;
}
@-webkit-keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
