.meeting video {
  width: 100%;
  height: 100%;
  background: #3E464B;
  object-fit: contain;
}
.meeting .voff-show {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #3E464B;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.meeting .voff-show .avatar-wrapper {
  text-align: center;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  color: #FFFFFF;
  line-height: 20px;
}
.meeting .voff-show .avatar-wrapper img.avatar {
  width: 68px;
  height: 68px;
}
.meeting .voff-show .avatar-wrapper .nick-name {
  position: relative;
}
.meeting .voff-show .avatar-wrapper .nick-name img {
  position: absolute;
  top: -50%;
  left: 50%;
  transform: translateX(-50%);
}
.meeting .leave-confirm-modal {
  width: 240px;
  height: 188px;
  color: #fff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  position: absolute;
  right: 16px;
  top: 55px;
  z-index: 3;
  padding: 0 10px;
  transition: all 0.3s;
}
.meeting .leave-confirm-modal .text {
  font-size: 14px;
  text-align: center;
  margin-top: 20px;
}
.meeting .leave-confirm-modal .handle {
  text-align: center;
  margin: 16px 0;
}
.meeting .leave-confirm-modal .handle .leave,
.meeting .leave-confirm-modal .handle .end {
  display: inline-block;
  width: 92px;
  line-height: 20px;
  background: #1c1c1e;
  border-radius: 10px;
  color: #00afef;
  font-size: 12px;
  cursor: pointer;
}
.meeting .leave-confirm-modal .handle .leave {
  margin-bottom: 12px;
}
.meeting iframe[name="white-board"] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  border: none;
}
.meeting .electorn-choose-desktop-media .ant-modal-content {
  border-radius: 4px;
}
.meeting .electorn-choose-desktop-media .ant-modal-content .ant-modal-header,
.meeting .electorn-choose-desktop-media .ant-modal-content .ant-modal-body {
  padding: 8px 15px;
}
.meeting .electorn-choose-desktop-media .ant-modal-content .tab-content {
  display: flex;
  flex-wrap: wrap;
  height: 400px;
  overflow-y: scroll;
}
.meeting .electorn-choose-desktop-media .ant-modal-content .tab-content .img-wrapper {
  flex: auto;
  min-width: 33%;
  max-width: 50%;
  width: 33%;
  max-height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
  border: 2px solid transparent;
}
.meeting .electorn-choose-desktop-media .ant-modal-content .tab-content .img-wrapper img {
  max-width: 100%;
  max-height: 85%;
}
.meeting .electorn-choose-desktop-media .ant-modal-content .tab-content .img-wrapper .name {
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.meeting .shared-desktop-alert {
  position: absolute;
  top: 115px;
  left: 100px;
  border: none;
  background: #fff;
  min-width: 200px;
}
.meeting .main-loading-wrapper {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  z-index: 1;
}
.meeting .main-loading-wrapper .ant-spin-nested-loading > div > .ant-spin .ant-spin-text {
  width: 200px;
  transform: translateX(-50%);
}
.ant-tooltip-inner {
  border-radius: 4px;
}
.toast {
  width: 240px;
  height: 188px;
  color: #fff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  position: absolute;
  left: 50%;
  bottom: 70px;
  transform: translateX(-50%);
  z-index: 2;
  padding: 0 10px;
  transition: all 0.3s;
}
.toast .text {
  font-size: 14px;
  text-align: center;
  margin-top: 20px;
}
.toast .handle {
  text-align: center;
  margin: 16px 0;
}
.toast .handle .leave,
.toast .handle .end {
  display: inline-block;
  width: 92px;
  line-height: 20px;
  background: #1c1c1e;
  border-radius: 10px;
  color: #00afef;
  font-size: 12px;
  cursor: pointer;
}
.toast .handle .leave {
  margin-bottom: 12px;
}
.toast-title {
  line-height: 40px;
  display: flex;
  justify-content: space-between;
}
.toast-title i.anticon-close-circle {
  cursor: pointer;
  line-height: inherit;
  font-size: 16px;
  opacity: 0.8;
}
.toast-title i.anticon-close-circle:hover {
  opacity: 1;
}
#toast-msg {
  background: rgba(0, 0, 0, 0.65);
  border-radius: 10px;
  min-width: 200px;
  color: #fff;
  text-align: center;
  font: bold 20px/40px italic;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s;
}
.inviteModal {
  position: absolute;
  width: 400px;
  height: 350px;
  text-align: center;
  padding: 10px;
  left: 50%;
  bottom: 15%;
  z-index: 4;
  transform: translateX(-50%);
  background: #fff;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2);
}
.inviteModal .close {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 15px;
  height: 15px;
  border-radius: 15px;
  background: #b9b2b4;
  color: rgba(50, 50, 50, 0.6);
  line-height: 13px;
  text-align: center;
  cursor: pointer;
  padding: 0 0 3px 1px;
}
.inviteModal textarea {
  display: block;
  width: 80%;
  height: 250px;
  margin: 10px auto;
  padding: 15px 20px;
  text-align: left;
}
.inviteModal textarea:focus {
  outline: 0;
}
.inviteModal button {
  width: 200px;
  border-radius: 5px ;
}
.ant-alert {
  z-index: 2;
  border-radius: 5px;
  position: absolute;
  top: 100px;
}
.openNotification {
  border-radius: 5px;
  padding: 5px 5px 3px;
  width: 300px;
  transform: translateX(80px);
}
.openNotification a.ant-notification-notice-close {
  top: 7px;
}
